export function resizeMapAreas(id, width) {
  // –∫–æ–¥, –∫–æ—Ç–æ—Ä—ã–π –¥–µ–ª–∞–µ—Ç —Ç–∞–∫, —á—Ç–æ–±—ã map —Ä–µ—Å–∞–π–∑–∏–ª—Å—è –∫–æ–≥–¥–∞ –º–µ–Ω—è–µ—Ç—Å—è —Ä–∞–∑–º–µ—Ä –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è
  var ImageMap = function (map) {
    var n,
      areas = map.getElementsByTagName("area"),
      len = areas.length,
      coords = [],
      previousWidth = width;
    for (n = 0; n < len; n++) {
      coords[n] = areas[n].coords.split(",");
    }
    this.resize = function () {
      var n,
        m,
        clen,
        x = document.body.clientWidth / previousWidth;
      for (n = 0; n < len; n++) {
        clen = coords[n].length;
        for (m = 0; m < clen; m++) {
          coords[n][m] *= x;
        }
        areas[n].coords = coords[n].join(",");
      }
      previousWidth = document.body.clientWidth;
      return true;
    };
    window.onresize = this.resize;
  };
  if (document.getElementById(id)) {
    let imageMap = new ImageMap(document.getElementById(id));
    imageMap.resize();
  }
}

// dataObj —Å–æ–¥–µ—Ä–∂–∏—Ç –∫–ª—é—á–∏ —Å–æ –∑–Ω–∞—á–µ–Ω–∏—è–º–∏, –∫–æ—Ç–æ—Ä—ã–µ –Ω–∞–¥–æ –ø–µ—Ä–µ–¥–∞–≤–∞—Ç—å –Ω–∞ —Å–µ—Ä–≤–µ—Ä
// backdropFunction - —Ñ—É–Ω–∫—Ü–∏—è, –æ–±–Ω–æ–≤–ª—è—é—â–∞—è backdrop
// snackbarFunctions - –æ–±—ä–µ–∫—Ç —Å —Ñ—É–Ω–∫—Ü–∏—è–º–∏, –∫–æ—Ç–æ—Ä—ã–µ –æ–±–Ω–æ–≤–ª—è—é—Ç snackbar
export function sendForm(dataObj, backdropFunction, snackbarFunctions) {
  const updateSnackbarMessage = snackbarFunctions.updateSnackbarMessage;
  const updateSnackbarOpen = snackbarFunctions.updateSnackbarOpen;
  const updateSnackbarSeverity = snackbarFunctions.updateSnackbarSeverity;

  const updateBackdrop = backdropFunction;


  const name = dataObj.name;
  const phone = dataObj.phone;
  const title = dataObj.title;

  const area = dataObj.area ?? "";
  const liter = dataObj.liter ?? "";
  const comment = dataObj.comment ?? "";


  updateBackdrop(true);
  // –ø—Ä–æ–≤–µ—Ä–∫–∞ –ø–æ–ª–µ–π
  if (name == "" || phone == "") {
    updateSnackbarMessage("–ù–µ–æ–±—Ö–æ–¥–∏–º–æ –∑–∞–ø–æ–ª–Ω–∏—Ç—å –æ–±–∞ –ø–æ–ª—è");
    updateSnackbarOpen(true);
    updateSnackbarSeverity("warning");
    updateBackdrop(false);
    return;
  }
  // –ø—Ä–æ–≤–µ—Ä–∫–∞ —Ç–µ–ª–µ—Ñ–æ–Ω–∞
  if (phone.includes("_")) {
    updateSnackbarMessage("–ù–µ–∫–æ—Ä—Ä–µ–∫—Ç–Ω–æ –≤–≤–µ–¥–µ–Ω –Ω–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω–∞");
    updateSnackbarOpen(true);
    updateSnackbarSeverity("warning");
    updateBackdrop(false);
    return;
  }

  fetch("/submit/submit-for-react.php", {
    method: "POST",
    body: JSON.stringify({
      area: area,
      title: title,
      phone: phone,
      name: name,
      liter: liter,
      comment: comment,
    }),
    headers: {
      "Content-Type": "application/json",
      // 'Content-Type': 'application/x-www-form-urlencoded',
    },
  }).then((res) => {
    //console.log("Request complete! response:", res);
    if (res.status === 200) {
      // –∑–∞–∫—Ä—ã—Ç–∏–µ –¥–∏–∞–ª–æ–≥–∞
      // —Å–æ–æ–±—â–µ–Ω–∏–µ –æ–± —É—Å–ø–µ—à–Ω–æ–π –æ—Ç–ø—Ä–∞–≤–∫–µ
      updateSnackbarMessage("–î–∞–Ω–Ω—ã–µ —É—Å–ø–µ—à–Ω–æ –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω—ã!");
      updateSnackbarOpen(true);
      updateSnackbarSeverity("success");

      // –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω–∏–µ –¥–∞–Ω–Ω—ã—Ö –≤ —Ç—Ä–µ–∫–µ—Ä—ã 
      setTimeout(() => {
        eval(
          `
          ym(71943988,'reachGoal','SEND');
          gtag('event','target',{'event_category':'FORM','event_action':'SEND',});
          fbq('track', 'Lead');
          `
        );
      }, 2000);
    } else {
      // —Å–æ–æ–±—â–µ–Ω–∏–µ –æ–± —É—Å–ø–µ—à–Ω–æ–π –æ—Ç–ø—Ä–∞–≤–∫–µ
      updateSnackbarMessage("–ü—Ä–æ–∏–∑–æ—à–ª–∞ –æ—à–∏–±–∫–∞. –ü–æ–ø—Ä–æ–±—É–π—Ç–µ –µ—â—ë —Ä–∞–∑.");
      updateSnackbarOpen(true);
      updateSnackbarSeverity("error");
    }
    updateBackdrop(false);
  });
}
